@import url('https://fonts.googleapis.com/css2?family=Manrope&family=Poppins:wght@200&display=swap');
*{
  box-sizing: border-box;
}
body{
  /* background-color: #17181f; */
  background-image: url(./3.jpg);
  background-size: cover;
  color: #ddd;
  font-family: 'Poppins';
  font-weight: 1000;
}
main{
  max-width: 360px;
  margin: 30px auto;
}
h1{
  text-align: center;
  color: #b9b2ff;
  margin: 0;
  font-size: 2.5rem;
  font-family: 'Manrope';
}
h1 span{
  font-size: 1rem;
  vertical-align: top;
  display: inline-block;
  margin-top: 7.7px;
}
form{
  margin-top: 20px;
}
form input{
  width: 100%;
  background-color: transparent;
  color: #ddd;
  border: 2px solid #0c0c0e;
  padding: 2px 5px;
  border-radius: 5px;
}
form input[type="datetime-local"]{
  color: #777;
}
form div.basics{
  display: flex;
  gap: 5px;
  margin-bottom: 5px;
}

form button{
  width: 100%;
  margin-top: 5px;
  border: 0;
  border-radius: 5px;
  padding: 5px;
  background-color: #ddd;
}
div.transactions{
  margin-top: 10px;
}
div.transaction{
  display: flex;
  padding: 5px 0;
  justify-content: space-between;
  border-top: 1px solid #30313d;
}
div.transaction:first-child{
  border-top: 0;
}
div.transaction div.name{
  font-size: 1rem;
}
div.transaction div.description{
  font-size: 0.7rem;
  color: #888;
}
div.right{
  text-align: right;
}
div.price.green{
  color: #15DA5B;
}
div.price.red{
  color: #F7191B;
}

div.transaction div.datetime{
  font-size: 0.7rem;
  color: #888;
}